@import './_reset.scss';
$mainColor: #ff0000;

.red{
    color: $mainColor;
}

@mixin 함수() {
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 4px;
    color: $mainColor;
    max-width: 500px;
    width:100%;
    margin: auto;
}

.my-alert{
    @include 함수();
    p{
        margin: 0;
    }
}
.my-alert-red{
    @extend .my-alert;
    background-color: lightblue;
}

